* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0.5em 0;
}
@media (min-width: 35em) {
  .col {
    display: table-cell;
  }
  .\31 {
    width: 5%;
  }
  .\33 {
    width: 22%;
  }
  .\34 {
    width: 30%;
  }
  .\35 {
    width: 40%;
  }
  .\32 {
    width: 15%;
  }
  .row {
    display: table;
    border-spacing: 1em 0;
  }
}
.row,
.w-100 {
  width: 100%;
}
.card:focus,
hr {
  outline: 0;
  border: solid #fa0;
}
.card,
pre {
  padding: 1em;
  border: solid #eee;
}
.btn:hover {
  opacity: 0.6;
}
a:hover {
  text-decoration: underline;
}
.c {
  max-width: 60em;
  padding: 1em;
  margin: auto;
  font: 1em/1.6 sans-serif;
}
