a {
  color: #fff;
  text-decoration: none;
}
.btn.primary {
  color: #fff;
  background: #fa0;
  border: solid #fa0;
}
pre {
  overflow: auto;
}
td,
th {
  padding: 1em;
  text-align: left;
  border-bottom: 1px solid #272727;
}
.btn {
  padding: 1em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  background: 0 0;
  border: solid;
  font: 0.7em nunito;
}

html {
  /* Change default typefaces here */
  font-size: 110%;
  -webkit-font-smoothing: antialiased;
}
/* Copy & Lists */
p {
  line-height: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 0;
}
ul,
ol {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
ul li,
ol li {
  line-height: 1.5rem;
}
ul ul,
ol ul,
ul ol,
ol ol {
  margin-top: 0;
  margin-bottom: 0;
}
blockquote {
  line-height: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
/* Headings */
h1,
h2,
h3,
h4,
h5,
h6 {
  /* Change heading typefaces here */
  font-family: sans-serif;
  margin-top: 1.5rem;
  margin-bottom: 0;
  line-height: 1.5rem;
  color: white;
}
h1 {
  font-size: 4.242rem;
  line-height: 4.5rem;
  margin-top: 3rem;
}
h2 {
  font-size: 2.828rem;
  line-height: 3rem;
  margin-top: 3rem;
}
h3 {
  font-size: 1.414rem;
}
h4 {
  font-size: 0.707rem;
}
h5 {
  font-size: 0.4713333333333333rem;
}
h6 {
  font-size: 0.3535rem;
}
/* Code blocks */
code {
  vertical-align: bottom;
}
/* Leading paragraph text */
.lead {
  font-size: 1.414rem;
}
/* Hug the block above you */
.hug {
  margin-top: 0;
}

body {
  background: #181818;
  color: #ddd;
}

h1 {
  margin-bottom: 1rem;
}
